'use strict';

import mongoose from 'mongoose';

var CategorySchema = new mongoose.Schema({
  title: {
  	type: String
  },
  description: {
  	type: String
  },
  thumb: {
   type:String
  },
  
  createdAt: {
    type: Date,
    default: Date.now
  },
  updatedAt: { 
    type: Date 
  }

});

export default mongoose.model('Category', CategorySchema);
